<template>
<div class="article-container">
<!--  -->
<el-card class="box-card">
    <div slot="header" class="clearfix">
      <!--  -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div>
        <el-form ref="form"
          :model="form"
          label-width="80px"
          size="small"
          >
          <!--  -->
          <el-form-item label=" :">
            <el-radio-group v-model="form.resource">
              <el-radio :label="null"></el-radio>
              <el-radio :label="0"></el-radio>
              <el-radio :label="1"></el-radio>
              <el-radio :label="2"></el-radio>
              <el-radio :label="3"></el-radio>
              <el-radio :label="4"></el-radio>
            </el-radio-group>
          </el-form-item>
          <!--  -->
          <el-form-item label=" :">
            <el-select v-model="channelId" placeholder="">
              <el-option
              label=""
              :value="null"
              ></el-option>
              <el-option
                :label="channel.name"
                :value="channel.id"
                v-for="(channel, index) in channels"
                :key="index"
                ></el-option>
            </el-select>
          </el-form-item>
          <!--  -->
          <el-form-item label=" :">
            <el-date-picker
              v-model="pubdate"
              type="datetimerange"
              start-placeholder=""
              end-placeholder=""
              :default-time="['00:00:00', '23:59:59']"
              format="yyyy-MM-dd"
              value-format="yyyy-MM-dd">
            </el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button type="info"
              @click="loadArticles(1)"
              :disabled="loading"></el-button>
          </el-form-item>
        </el-form>
    </div>
</el-card>
<!--  -->
<el-card class="box-card">
  <div slot="header" class="clearfix">
  <span> <span>{{ totalCount }}</span> </span>
  </div>
  <!--  -->
  <el-table
    v-loading="loading"
    element-loading-text=""
    :data="articles"
    stripe
    style="width: 100%">
      <el-table-column
        prop=""
        label=""
        width="180">
        <template slot-scope="scope">
          <!--   -->
          <!-- <img v-if="scope.row.cover.images[0]" class="coverImage" :src="scope.row.cover.images[0]" alt="">
          <img v-else class="coverImage" src="./error.3f7b1f94.gif" alt=""> -->

          <!-- express  -->
            <el-image
              style="width: 100px; height: 100px"
              :src="scope.row.cover.images[0]"
              :fit="fits[2]"
              lazy>
              <div slot="placeholder" class="image-slot">
                <span class="dot">...</span>
              </div>
              </el-image>
        </template>
      </el-table-column>
      <el-table-column
        prop="title"
        label=""
        width="180">
      </el-table-column>
      <el-table-column
        label="">
        <!--  -->
        <template slot-scope="scope">
          <!-- datatype  -->
          <el-tag :type="articleStatus[scope.row.status].type">{{ articleStatus[scope.row.status].text }}</el-tag>
          <!-- <el-tag v-else-if="scope.row.status === 2" type="success"></el-tag>
          <el-tag v-else-if="scope.row.status === 1" type="info"></el-tag>
          <el-tag v-else-if="scope.row.status === 3" type="warning"></el-tag>
          <el-tag v-else-if="scope.row.status === 4" type="danger"></el-tag> -->
        </template>
      </el-table-column>
      <el-table-column
        prop="pubdate"
        label=""
        width="180">
      </el-table-column>
      <el-table-column
        prop="name"
        label=""
        width="180"
        >
        <template slot-scope="scope">
          <el-button
            size="small"
            circle
            icon="el-icon-edit"
            type="primary"
            @click="onUpdateArticle(scope.row.id)">
          </el-button>
          <el-button
            size="mini"
            type="danger"
            icon="el-icon-delete"
            circle
            @click="onDeleteArticle(scope.row.id)">
          </el-button>
        </template>
      </el-table-column>
  </el-table>
  <!--  -->
  <el-pagination
    background
    layout="prev, pager, next"
    @current-change="onPageChange"
    :page-size="perPage"
    :current-page="page"
    :total="totalCount"
    :disabled="loading">
  </el-pagination>
</el-card>
</div>
</template>
<script>
import {
  getArticles,
  getArticleChannel,
  delArticle
} from '@/api/article'

export default {
  name: 'ArticleIndex',
  props: {},
  components: {},
  data () {
    return {
      form: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: null,
        desc: ''
      },
      articles: [],
      articleStatus: [
        { status: 0, text: '', type: 'info' },
        { status: 1, text: '', type: '' },
        { status: 2, text: '', type: 'success' },
        { status: 3, text: '', type: 'warning' },
        { status: 4, text: '', type: 'danger' }
      ],
      fits: ['fill', 'contain', 'cover', 'none', 'scale-down'],
      url: '',
      perPage: 15,
      totalCount: 0,
      page: 1, // 
      channels: [], // 
      channelId: null, // 
      pubdate: null, // 
      loading: true

    }
  },
  computed: {},
  watch: {},
  methods: {
    onSubmit () {
      console.log('submit!')
    },
    // page page=1
    loadArticles (page = 1) {
      this.loading = true
      getArticles({
        // page page
        page,
        per_page: this.perPage,
        status: this.form.resource,
        channel_id: this.channelId,
        begin_pubdate: this.pubdate ? this.pubdate[0] : null,
        end_pubdate: this.pubdate ? this.pubdate[1] : null
      }).then(res => {
        // console.log(res)
        // res.data.data=[page: 1, per_page: 10, results: [], total_count: 118684 ...]
        const { results, total_count: totalCount } = res.data.data
        this.articles = results
        this.totalCount = totalCount
        this.loading = false
      })
    },
    onPageChange (page) {
      this.loading = true
      this.loadArticles(page)
    },
    loadChannel () {
      getArticleChannel()
        .then(res => {
          // console.log(res)
          this.channels = res.data.data.channels
        })
    },
    //  
    onDeleteArticle (articleId) {
      console.log(articleId.toString())
      console.log(articleId)
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          delArticle(articleId).then(res => {
            console.log(articleId)
            this.$message({
              type: 'success',
              message: '!'
            })
            this.loadArticles(this.page)
          })
            .catch(err => {
              console.log(err)
            })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    onUpdateArticle (articleId) {
      // js
      this.$router.push(`/publish?id=${articleId}`)
    }
  },
  created () {
    // 
    this.loadArticles(1)
    this.loadChannel()
  },
  mounted () {},
  beforeDestroy () {}
}
</script>
<style lang='less' scoped>
.box-card {
  margin-bottom: 20px;
}
.item {
  margin-bottom: 10px;
}
.coverImage {
  width: 100px;
  background-size: cover;
}
</style>
