import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '@/views/login'
import Layout from '@/views/layout'
import Home from '@/views/Home/home'

// 
import publishIndex from '@/views/publish'
// 
import ArticleIndex from '@/views/article'
// 
import imageIndex from '@/views/image'
//  
import commentIndex from '@/views/comment'
// 
import accountIndex from '@/views/account'
// 
import fansIndex from '@/views/fans'

Vue.use(VueRouter)

const routes = [
  // 
  {
    path: '/login',
    component: Login,
    name: 'login'
  },
  // layout  
  {
    path: '/',
    component: Layout,
    name: '',
    children: [
      {
        path: '',
        component: Home,
        name: 'home'
      },
      // 
      {
        path: '/article',
        component: ArticleIndex,
        name: 'ArticleIndex'
      },
      // 
      {
        path: '/publish',
        component: publishIndex,
        name: 'publishIndex'
      },
      // 
      {
        path: '/image',
        component: imageIndex,
        name: 'imageIndex'
      },
      // 
      {
        path: '/comment',
        component: commentIndex,
        name: 'commentIndex'
      },
      // 
      {
        path: '/profile',
        component: accountIndex,
        name: 'accountIndex'
      },
      // 
      {
        path: '/fans',
        component: fansIndex,
        name: 'fansIndex'
      }
    ]
  }
]

const router = new VueRouter({
  routes
})
// 
router.beforeEach((to, from, next) => {
  const user = JSON.parse(window.localStorage.getItem('user'))
  // console.log(to, from, user)
  // 
  if (to.path !== '/login') {
    // 
    if (user) {
      next()
    } else {
      next('/login')
    }
  // next()
  } else {
    next()
  }
})
export default router
