<template>
  <div class="login-container">
      <el-form ref="login-form" :model="user" :rules="rules" class="login-form">
  <el-form-item prop="mobile">
    <el-input v-model="user.mobile" placeholder=""></el-input>
  </el-form-item>
  <el-form-item prop="code">
    <el-input v-model="user.code" placeholder=""></el-input>
  </el-form-item>
  <el-form-item prop="agree">
    <el-checkbox v-model="user.agree"></el-checkbox>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="onlogin" style="width: 100%" :loading="loginLoding"></el-button>
  </el-form-item>
</el-form>
  </div>
</template>

<script>
// 
import { loginTo } from '@/api/user'
export default {
  name: 'LoginIndex',
  components: {},
  props: {},
  data () {
    return {
      user: {
        mobile: '13911111111',
        code: '246810',
        agree: ''
      },
      loginLoding: false,
      rules: {
        mobile: [
          { pattern: /^1[3|5|7|8]\d{9}$/, message: '', trigger: 'change' },
          { required: true, message: '', trigger: 'blur' }
        ],
        code: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^\d{6}$/, message: '', trigger: 'change' }
        ],
        agree: [
          {
            validator: (rule, value, callback) => {
              if (value) {
                callback()
              } else {
                callback(new Error(''))
              }
              // console.log(value)
            }
          }
        ]
      }
    }
  },
  computed: {},
  watch: {},
  created () {},
  mounted () {},
  methods: {
    onlogin () {
      // 
      this.$refs['login-form'].validate(valid => {
        // console.log(valid) valid false/true
        if (!valid) {
          return
        }
        //  
        this.login()
      })
    },
    login () {
      // 
      // const user = this.user
      this.loginLoding = true
      loginTo(this.user).then(res => {
        // 
        this.$message({
          message: '',
          type: 'success'
        })
        // 
        this.loginLoding = false
        // token
        // console.log(1)
        window.localStorage.setItem('user', JSON.stringify(res.data.data))
        // 
        this.$router.push(
          {
            name: 'home'
          }
        )
      }).catch(err => {
        console.log(err)
        this.$message.error('')
        this.loginLoding = false
      })
    }
  }
}
</script>

<style lang="less" scoped>
  .login-container {
      position: fixed;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: url(./login_bg.jpg) no-repeat;
      background-size: cover;
      .login-form {
          background-color: #fff;
          padding: 50px;
      }
  }
</style>
